/* Admin Dashboard Styles */
.admin-container {
  max-width: 1200px;
  margin: 0 auto;
}

.admin-header {
  margin-bottom: 30px;
}

.admin-nav {
  display: flex;
  gap: 20px;
  margin-bottom: 30px;
  border-bottom: 1px solid #eee;
}

.admin-nav-link {
  padding: 12px 0;
  text-decoration: none;
  color: #666;
  font-weight: 500;
  border-bottom: 2px solid transparent;
  transition: all 0.2s;
}

.admin-nav-link:hover,
.admin-nav-link.active {
  color: #007bff;
  border-bottom-color: #007bff;
}

.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
  margin-bottom: 30px;
}

.stat-card {
  background: white;
  padding: 20px;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

.stat-breakdown div {
  margin: 8px 0;
  font-size: 14px;
  color: #666;
}

.users-table {
  background: white;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

.user-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  border-bottom: 1px solid #f8f9fa;
}

.user-row:last-child {
  border-bottom: none;
}

.user-info {
  display: flex;
  align-items: center;
  gap: 15px;
}

.user-avatar img,
.user-avatar .avatar-placeholder {
  width: 50px;
  height: 50px;
  border-radius: 50%;
}

.user-avatar .avatar-placeholder {
  background: #007bff;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 600;
}

.user-details h4 {
  margin: 0 0 5px 0;
  color: #333;
}

.user-details p {
  margin: 0;
  color: #666;
  font-size: 14px;
}

.approval-badge {
  font-size: 11px;
  padding: 2px 6px;
  border-radius: 3px;
  margin-left: 8px;
}

.approval-badge.approved {
  background: #d4edda;
  color: #155724;
}

.approval-badge.pending {
  background: #fff3cd;
  color: #856404;
}

.user-actions {
  display: flex;
  gap: 10px;
}

/* Simple Charts */
.charts-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
}

.chart-card {
  background: white;
  padding: 20px;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

.simple-chart {
  display: flex;
  gap: 10px;
  align-items: end;
  height: 200px;
  margin-top: 20px;
}

.chart-bar {
  flex: 1;
  background: #007bff;
  display: flex;
  align-items: end;
  justify-content: center;
  color: white;
  font-size: 12px;
  padding: 10px 5px;
  text-align: center;
  line-height: 1.2;
}

.chart-bar:nth-child(2) {
  background: #28a745;
}

.chart-bar:nth-child(3) {
  background: #ffc107;
  color: #333;
}

/* Expertise Requests */
.admin-expertise-requests .users-header {
  margin-bottom: 20px;
}

.admin-expertise-requests .users-header h3 {
  margin-bottom: 5px;
}

.admin-expertise-requests .users-header p {
  color: #666;
  margin: 0;
}

.expertise-requests-list {
  background: white;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

.expertise-request-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  border-bottom: 1px solid #f8f9fa;
}

.expertise-request-item:last-child {
  border-bottom: none;
}

.request-info {
  display: flex;
  align-items: center;
  gap: 20px;
  flex: 1;
}

.expertise-details {
  margin-left: 20px;
}

.expertise-details h5 {
  margin: 0 0 5px 0;
  font-size: 14px;
  color: #666;
  font-weight: 500;
}

.expertise-name {
  font-weight: 600;
  color: #333;
  margin: 0 0 5px 0;
  font-size: 16px;
}

.expertise-details small {
  color: #999;
  font-size: 12px;
}

.request-actions {
  display: flex;
  gap: 10px;
}

.no-requests {
  text-align: center;
  padding: 40px;
  color: #666;
  background: white;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

@media (max-width: 768px) {
  .user-row,
  .expertise-request-item {
    flex-direction: column;
    align-items: flex-start;
    gap: 15px;
  }
  
  .user-actions,
  .request-actions {
    align-self: stretch;
    justify-content: flex-end;
  }
  
  .request-info {
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
  }
  
  .expertise-details {
    margin-left: 0;
  }
}
